import { useRTL } from '@hooks/useRTL'
import useJwt from '@src/auth/jwt/useJwt'
import useJwt2 from '@src/auth/jwt/useJwt2'

import { selectThemeColors } from '@utils'
import React, { Fragment, useEffect, useRef, useState, useMemo } from 'react'
import { ChevronLeft, FileText, Search, UploadCloud } from 'react-feather'
import { Link, useHistory } from 'react-router-dom'
import Select from 'react-select'
import { Button, Card, CardBody, CardHeader, CardTitle, Col, CustomInput, Form, FormGroup, Input, Label, Row, Spinner, Table } from 'reactstrap'
import XLSX from "xlsx"
import { API_BASE_URL } from "../../../../../../Configurables"
import { ExportCSV } from '../../../../../helper'
import { Error, Success } from '../../../../../viewhelper'
import { Upload } from "antd"
const { Dragger } = Upload
// ** Styles
import '@styles/react/libs/noui-slider/noui-slider.scss'
import "antd/dist/antd.css"
import { toast } from 'react-toastify'
import UploadCsvGroup from './UploadCsvGroupv2'

const CreateGroup = () => {
    const [pointRuleloading, setPointRuleloading] = useState(false)
    const typeRef = useRef()
    const history = useHistory()
    const [selectRuleProvider, setRuleProvider] = useState({})
    // const [file, setFile] = useState(null)
    const [filePrevw, setFilePrevw] = useState(null)
    const [isRtl, setIsRtl] = useRTL()
    const [divisions, setDivision] = useState([])
    const [districts, setDistrict] = useState([])
    const [thanas, setThana] = useState([])
    const [incomeSources, setIncomeSource] = useState([])
    const [occupations, setOccupation] = useState([])
    const [businessList, setBusinessList] = useState([])
    const [customcodecsvurl, setcustomcodecsvurl] = useState(null)
    const [userInput, setUserInput] = useState({
        group_name: '',
        type: null, //int
        creation_type: 2,
        gender: [],
        age: [],
        division: [],
        district: [],
        thana : [],
        kyc_status : [],
        id_type : [0], //currently we only using NID only, thats why by default sending 0
        //above attribute are for bot type, customer and merchant, below are applicable only for merchant
        occupation_id : [],
        income_source_id : [],
        is_bulk_pay_allow : [],
        is_web_login : [],
        merchant_nature : [],
        advanced_business_type : [],
        advanced_info : [],
        monthly_income: 0
    })

    const handleChange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
    console.log(userInput)

    useEffect(() => {
        useJwt.BDAddressList().then(res => {
            setDivision(res.data.payload)
        }).catch(err => {
            //console.log(err.response)
            Error(err)
        })
        useJwt2.getBusinesstype().then(async res => {
            setIncomeSource(res.data.income_source.map(item => { return {value: item.id, label: item.name} }))
            setOccupation(res.data.occupation_bussiness.map(item => { return {value: item.id, label: item.occupation_name} }))
        }).catch(err => {
            //console.log(err.response)
            Error(err)
        })
    }, [])

    const [ShowUpload, setShowUpload] = useState(true)
    const [rowNumber, setRowNumber] = useState(0)
    const [File, setFile] = useState(null)
    // const [Filename, setFilename] = useState('')
    const handleFile = (file /*:File*/) => {
        /* Boilerplate to set up FileReader */
        const reader = new FileReader()
        const rABS = !!reader.readAsBinaryString
        reader.onload = (e) => {
            /* Parse data */
            const bstr = e.target.result
            const wb = XLSX.read(bstr, { type: rABS ? "binary" : "array" })
            /* Get first worksheet */
            const wsname = wb.SheetNames[0]
            const ws = wb.Sheets[wsname]
            /* Convert array of arrays */
            const data = XLSX.utils.sheet_to_json(ws, {
                header: 1
            })
            setRowNumber(data.length - 1)
            console.log(
                "data",
                data.map((x) => { return { MSISDN: x[0] } }).filter(y => !!y).splice(1)
            )
            // setModalMSISDN(data.map((x) => { return { MSISDN: x[0] } }).filter(y => !!y).splice(1))
            /* Update state */
            //   this.setState({ data: data, cols: make_cols(ws["!ref"]) })
        }
        if (rABS) reader.readAsBinaryString(file)
        else reader.readAsArrayBuffer(file)
    }


    function beforeUpload(file) {
        console.log(file)
        const isCSV = file.type === 'application/vnd.ms-excel' || file.type === 'text/csv'
        if (!isCSV) {
            toast.error("Only CSV file can be uploaded", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined
            })
        }
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
            toast.error("File must smaller than 2MB!", {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined
            })
        }
        return isCSV && isLt2M
    }

    function getBase64(img, callback) {
        const reader = new FileReader()
        reader.addEventListener('load', () => callback(reader.result))
        reader.readAsDataURL(img)
    }

    const props = {
        name: 'file',
        multiple: false,
        action: `${API_BASE_URL}/fackupload`,
        beforeUpload,
        async onChange(info) {
            console.log('info', info)
            if (info.file.status === 'uploading') {
                // setloading(true)
                return
            }
            if (info.file.status === 'done') {
                // Get this url from response in real world.
                await getBase64(info.file.originFileObj, imageUrl => {
                    console.log(info.file.originFileObj, imageUrl)
                    handleFile(info.file.originFileObj)
                    setFile(imageUrl)
                })
            } else if (info.file.status === 'error') {
                toast.error(`${info.file.name} file upload failed.`)
            }
        }
    }

    const onSubmit = async (e) => {
        e.preventDefault()
        if (!customcodecsvurl && userInput.creation_type === 1) {
            Error({response: { status : 404, data: { message: 'Please wait for file upload'} }})
            return 0
        }
        setPointRuleloading(true)
        // console.log({ ...userInput, report_file: File })
        useJwt.createCentralGroup({ ...userInput, report_file: customcodecsvurl }).then((response) => {
            setPointRuleloading(false)

            // console.log(response)
            Success(response)
            history.push('/AllCentralGroups')
        }).catch((error) => {
            Error(error)
            setPointRuleloading(false)
            console.log(error.response)
        })
        // if (userInput.creation_type === 2) {

        // } else {
        //     useJwt.createCentralGroup({ ...userInput, report_file: File }).then((response) => {
        //         setPointRuleloading(false)

        //         console.log(response)
        //         Success(response)
        //         history.push('/AllCentralGroups')
        //     }).catch((error) => {
        //         Error(error)
        //         setPointRuleloading(false)
        //         console.log(error.response)
        //     }) 
        // }
    }

    const uploadCsvGroupMemo = useMemo(() => {
        return userInput.creation_type === 1 ? (
          <UploadCsvGroup setcustomcodecsvurl={setcustomcodecsvurl} handleFile={handleFile} />
        ) : null
      }, [userInput.creation_type, setcustomcodecsvurl, handleFile])

      
    return (
        <Fragment>
            <Button.Ripple className='mb-1' color='primary' tag={Link} to='/AllCentralGroups' >
                <div className='d-flex align-items-center'>
                    <ChevronLeft size={17} style={{ marginRight: '5px' }} />
                    <span >Back</span>
                </div>
            </Button.Ripple>
            <Form style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                <Card>
                    <CardHeader className='border-bottom'>
                        <CardTitle tag='h4'>Create Group</CardTitle>
                    </CardHeader>
                    <CardBody style={{ paddingTop: '15px' }}>
                        <Row>
                            <Col sm="4" >
                                <FormGroup>
                                    <Label for="group_name">Group Name<span style={{ color: 'red' }}>*</span></Label>
                                    <Input type="text"
                                        name="group_name"
                                        id='group_name'
                                        value={userInput.group_name}
                                        onChange={handleChange}
                                        required
                                        placeholder="group name..."
                                    />
                                </FormGroup>
                            </Col>
                            <Col sm="4" >
                                <FormGroup>
                                    <Label for="type">Group Type<span style={{ color: 'red' }}>*</span></Label>
                                    <Select
                                        ref={typeRef}
                                        theme={selectThemeColors}
                                        maxMenuHeight={200}
                                        className='react-select'
                                        classNamePrefix='select'
                                        // value={[{ value: 4, label: 'All' }, { value: 1, label: 'Customer' }, { value: 2, label: 'Agent' }, { value: 3, label: 'Merchant' }].find(i => i.value === userInput.type)}
                                        value={[{ value: 1, label: 'Customer' }, { value: 3, label: 'Merchant' }].find(i => i.value === userInput.type)}
                                        onChange={(selected) => {
                                            if (selected.value !== 1 && selected.value !== 2 && selected.value !== 4) {
                                                setUserInput({ ...userInput, type: selected.value, creation_type: 1 })
                                            } else {
                                                setUserInput({ ...userInput, type: selected.value, creation_type: 2 })
                                            }
                                        }}
                                        // options={[{ value: 4, label: 'All' }, { value: 1, label: 'Customer' }, { value: 2, label: 'Agent' }, { value: 3, label: 'Merchant' }]}
                                        options={[{ value: 1, label: 'Customer' }, { value: 3, label: 'Merchant' }]}
                                    />
                                    {
                                        userInput.type === 4 && <span style={{ fontSize: '12px', color: 'blue' }} className='d-flex justify-content-end'>'All' type targets only agent and customer group.</span>
                                    }
                                </FormGroup>
                                <Input
                                    required
                                    style={{
                                        opacity: 0,
                                        width: "100%",
                                        height: 0
                                        // position: "absolute"
                                    }}
                                    onFocus={e => typeRef.current.select.focus()}
                                    value={userInput.type || ''}
                                    onChange={e => ''}
                                />
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
                {
                    (userInput.type) && <>
                        <Row>
                            <Col sm="12" className='mb-1'>
                                <Label className='d-block'><h6>Group Creation<span style={{ color: 'red' }}>*</span></h6></Label>
                                {
                                    (userInput.type === 1 || userInput.type === 3 || userInput.type === 4) && <FormGroup check inline className='mr-3'>
                                        <CustomInput type='radio' name='gp' id='gp' checked={userInput.creation_type === 2} label='Group Profiling'
                                            onChange={() => {
                                                setUserInput({ ...userInput, creation_type: 2 })
                                            }}
                                        />
                                    </FormGroup>
                                }
                                <FormGroup check inline>
                                    <CustomInput type='radio' name='bp' id='bp' checked={userInput.creation_type === 1} label='Bulk Upload'
                                        onChange={() => {
                                            setUserInput({ ...userInput, creation_type: 1 })
                                        }}
                                    />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Card>
                            <CardBody>
                                {
                                    userInput.creation_type === 2 && <Row>
                                        <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Gender</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='behavior'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, gender: a })
                                                    }}
                                                    options={[{ value: 'm', label: 'Male' }, { value: 'f', label: 'Female' }]}
                                                />
                                            </FormGroup>
                                        </Col>
                                        <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Age (years)</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='behavior'
                                                    isMulti
                                                    style={{ width: '200px' }}
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, age: a })
                                                    }}
                                                    options={[{ value: '18 - 25', label: '18 - 25' }, { value: '26 - 35', label: '26 - 35' }, { value: '36 - 45', label: '36 - 45' }, { value: '46 - 55', label: '46 - 55' }, { value: '56 - 65', label: '56 - 65' }, { value: '66 - 75', label: '66 - 75' }, { value: '76 - 85', label: '76 - 85' }, { value: '86 - 95', label: '86 - 95' }]}
                                                />
                                            </FormGroup>
                                        </Col>
                                        <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>KYC Status</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='kyc_status'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, kyc_status: a })
                                                    }}
                                                    options={[{ value: 0, label: 'Not Submitted' }, { value: 1, label: 'Approved' }, { value: 2, label: 'Processing' }, { value: 3, label: 'NID Not Found' }, { value: 4, label: 'NID Match But Selfi Not Match' }, { value: 5, label: 'Selfi Processing' }]}
                                                />
                                            </FormGroup>
                                        </Col>

                                        <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Location</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={false}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    placeholder='select division'
                                                    isMulti
                                                    menuPlacement='auto'
                                                    onChange={e => {
                                                        if (!e) {
                                                            setDistrict([])
                                                        } else {
                                                            const a = e.map(ee => ee.value.id)
                                                            setUserInput({ ...userInput, division: a })
                                                            setDistrict(() => {
                                                                const dist = []
                                                                for (const se of e) {
                                                                    for (const d of se.value.dis) {
                                                                        dist.push(d)
                                                                    }
                                                                }
                                                                return dist
                                                            })
                                                        }
                                                    }}
                                                    options={divisions.map(d => { return { value: { id: d.id, dis: d.districts }, label: d.name } })}
                                                    isLoading={divisions.length === 0}
                                                />
                                            </FormGroup>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={false}
                                                    maxMenuHeight={200}
                                                    menuPlacement='auto'
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    isLoading={districts.length === 0}
                                                    isMulti
                                                    placeholder='select district'
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value.id)
                                                        setUserInput({ ...userInput, district: a })
                                                        setThana(() => {
                                                            const thna = []
                                                            for (const se of e) {
                                                                for (const d of se.value.thana) {
                                                                    thna.push(d)
                                                                }
                                                            }
                                                            return thna
                                                        })
                                                    }}
                                                    name='region'
                                                    options={districts.map(d => { return { value: { id: d.id, thana: d.thana }, label: d.name } })}
                                                />
                                            </FormGroup>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={false}
                                                    maxMenuHeight={200}
                                                    menuPlacement='auto'
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    isLoading={thanas.length === 0}
                                                    isMulti
                                                    placeholder='select thana'
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, thana: a })
                                                    }}
                                                    name='region'
                                                    options={thanas.map(d => { return { value: d.id, label: d.name } })}
                                                />
                                            </FormGroup>
                                        </Col>

                                        { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Monthly Income</h6></div>
                                            <Row>
                                                <Col sm='12'>
                                                    <FormGroup>
                                                        <Input type="text"
                                                            name="monthly_income"
                                                            id='monthly_income'
                                                            rows='4'
                                                            value={userInput.monthly_income}
                                                            onChange={handleChange}
                                                            required
                                                            placeholder="per month income here..."
                                                        />
                                                    </FormGroup>
                                                </Col>
                                            </Row>
                                        </Col>
                                        }

                                        { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Income Source</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='behavior'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, income_source_id: a })
                                                    }}
                                                    options={incomeSources}
                                                />
                                            </FormGroup>
                                        </Col>}
                                       { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Occupation</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='behavior'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, occupation_id: a })
                                                    }}
                                                    options={occupations}
                                                />
                                            </FormGroup>
                                        </Col>}
                                        { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Advanced Info</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='advanced_info'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, advanced_info: a })
                                                    }}
                                                    options={[{ value: 0, label: 'Not Submitted' }, { value: 1, label: 'Approved' }, { value: 2, label: 'Processing' }, { value: 3, label: 'Rejected' }]}
                                                />
                                            </FormGroup>
                                        </Col>}
                                        { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Business Type</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='advanced_business_type'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, advanced_business_type: a })
                                                    }}
                                                    options={[{ value: 1, label: 'Online' }, { value: 2, label: 'Offline' }, { value: 3, label: 'Both' }]}
                                                />
                                            </FormGroup>
                                        </Col>}
                                        { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Merchant Nature</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='merchant_nature'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, merchant_nature: a })
                                                    }}
                                                    options={[{ value: 'Individual', label: 'Individual' }, { value: 'Business', label: 'Business' }]}
                                                />
                                            </FormGroup>
                                        </Col>}
                                        { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Web Login</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='is_web_login'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, is_web_login: a })
                                                    }}
                                                    options={[{ value: true, label: 'True' }, { value: false, label: 'False' }]}
                                                    
                                                />
                                            </FormGroup>
                                        </Col>}
                                        { userInput.type === 3 && <Col sm='3' className='mb-1'>
                                            <div style={{ backgroundColor: '#006496', padding: '6px 15px', marginBottom: '15px' }}><h6 className='m-0 text-white'>Bulk Pay Allow</h6></div>
                                            <FormGroup>
                                                <Select
                                                    theme={selectThemeColors}
                                                    isClearable={true}
                                                    maxMenuHeight={200}
                                                    className='react-select'
                                                    classNamePrefix='select'
                                                    name='is_bulk_pay_allow'
                                                    isMulti
                                                    onChange={e => {
                                                        const a = e.map(ee => ee.value)
                                                        setUserInput({ ...userInput, is_bulk_pay_allow: a })
                                                    }}
                                                    options={[{ value: true, label: 'True' }, { value: false, label: 'False' }]}
                                                    
                                                />
                                            </FormGroup>
                                        </Col>}
                                    </Row>
                                }
                                {/* {
                                    userInput.creation_type === 1 && <UploadCsvGroup setcustomcodecsvurl={setcustomcodecsvurl} handleFile={handleFile}/>
                                } */}
                                {uploadCsvGroupMemo}
                            </CardBody>
                        </Card>
                        {
                            userInput.creation_type === 1 && <Row>
                                <Col sm='12' className='text-center mt-1'>
                                    <h6>Total Item</h6>
                                    <h1>{rowNumber}</h1>
                                </Col>
                            </Row>
                        }
                    </>
                }
                <Row>
                    <Col sm="12" className='text-center'>
                        {
                            pointRuleloading ? <Button.Ripple color='primary' disabled style={{ marginTop: '25px' }}>
                                <Spinner color='white' size='sm' />
                                <span className='ml-50'>Loading...</span>
                            </Button.Ripple> : <Button.Ripple color='primary' type="submit" style={{ marginTop: '25px' }}>
                                <span >Submit</span>
                            </Button.Ripple>
                        }
                    </Col>
                </Row>

            </Form>
        </Fragment>
    )
}

export default CreateGroup